      p La implementación de la consulta previa como eje fundamental normativo implica: la aplicación estricta del Convenio 169 de la OIT y la Ley 21 de 1991, garantizando que toda actividad turística en territorios étnicos cuente con el consentimiento libre, previo e informado, el diseño de procedimientos culturalmente apropiados para la obtención del consentimiento comunitario y la creación de mecanismos de participación efectiva, asegurando la intervención directa de las comunidades en todas las fases de los proyectos turísticos.
